import React from 'react';
import PropTypes from 'prop-types'

function AnswerOption(props) {
    return (
        <li className={`answerOption ${props.className}`}>
            <input
                type="radio"
                className="radioCustomButton"
                name="radioGroup"
                checked={props.answerType.join(',') === props.answer}
                id={props.answerType}
                value={props.answerType}
                disabled={props.answer}
                onChange={props.onAnswerSelected}
            />
            <label className="radioCustomLabel" htmlFor={props.answerType}>
                <span>{props.answerContent}</span>
                <img src={props.answerImage} alt=""/>

            </label>
        </li>
    );
}

AnswerOption.propTypes = {
    answerType: PropTypes.arrayOf(PropTypes.string).isRequired,
    answerContent: PropTypes.string.isRequired,
    answer: PropTypes.string.isRequired,
    answerImage: PropTypes.string,
    onAnswerSelected: PropTypes.func.isRequired
};
export default AnswerOption;