import logo from './logo.svg';

import React, {Component} from "react";
import './App.css';
import Quiz from "./components/Quiz";
import Result from "./components/Result";
import StartPage from "./components/StartPage";
import quizQuestions from './api/quizQuestions';
import update from 'immutability-helper';

const initialState = {
    start: false,
    counter: 0,
    questionId: 1,
    question: '',
    answerOptions: [],
    answer: '',
    answersCount: {
        golosiivo: 0,
        peremoga: 0,
        nyvky: 0,
        soloma: 0,
        feofaniya: 0,
        natalka: 0,
    },
    result: ''
}


class App extends Component {
    constructor(props) {
        super(props);
        this.state = initialState;
        this.handleAnswerSelected = this.handleAnswerSelected.bind(this);
    }

    final = () => this.state.result ? 'final': '';

    shuffleArray(array) {
        let currentIndex = array.length, temporaryValue, randomIndex;
// Пока остаются элементы, чтобы перемешать …
        while (0 !== currentIndex) {
// Выберите оставшийся элемент …
            randomIndex = Math.floor(Math.random() * currentIndex);
            currentIndex -= 1;
// И поменяйте местами с текущим элементом.
            temporaryValue = array[currentIndex];
            array[currentIndex] = array[randomIndex];
            array[randomIndex] = temporaryValue;
        }
        return array;
    }


    componentDidMount() {
        const shuffledAnswerOptions = this.createAnswerOptions();

        this.setState({
            question: quizQuestions[0].question,
            answerOptions: shuffledAnswerOptions[0]
        });
    }

    createAnswerOptions = () => {
        const shuffledAnswerOptions = quizQuestions.map((question) => this.shuffleArray(question.answers));
        return shuffledAnswerOptions;
    }



    handleAnswerSelected(value) {
        this.setUserAnswer(value);

        if (this.state.questionId < quizQuestions.length) {
            setTimeout(() => this.setNextQuestion(), 0);
        } else {
            console.log('here')
            setTimeout(() => this.setResults(this.getResults()), 0);
        }
    }

    setUserAnswer(answer) {
        let updateObj = {};
        answer.split(',').forEach(item => {
            updateObj = {
                ...updateObj,
                [item]: {$apply: (currentValue) => currentValue + 1}
            }
        });

        const updatedAnswersCount = update(this.state.answersCount, updateObj);
        this.setState(prev => ({
            answersCount: updatedAnswersCount,
            answer: answer
        }));
        this.forceUpdate()

    }

    setNextQuestion() {
        const counter = this.state.counter + 1;
        const questionId = this.state.questionId + 1;
        this.setState({
            counter: counter,
            questionId: questionId,
            question: quizQuestions[counter].question,
            answerOptions: quizQuestions[counter].answers,
            answer: ''
        });
    }

    getResults() {
        const answersCount = this.state.answersCount;
        const answersCountKeys = Object.keys(answersCount);
        const answersCountValues = answersCountKeys.map((key) => answersCount[key]);
        const maxAnswerCount = Math.max.apply(null, answersCountValues);
        return answersCountKeys.filter((key) => answersCount[key] === maxAnswerCount);
    }

    setResults(result) {
        this.setState({result: result[0]});
        console.log(this.state.result);
    }

    renderQuiz() {
        return (
            <Quiz
                answer={this.state.answer}
                answerOptions={this.state.answerOptions}
                questionId={this.state.questionId}
                question={this.state.question}
                questionTotal={quizQuestions.length}
                onAnswerSelected={this.handleAnswerSelected}
            />
        );
    }

    renderResult() {
        return (
            <Result quizResult={this.state.result} bedinAgainGame = {this.bedinAgainGame}/>
        );
    }

    renderStart() {
        return (
            <StartPage startGame = {this.startGame}/>
        );
    };

    startGame = () =>{
        this.setState({
            start: true
        })
    }
    bedinAgainGame = () =>{
        const shuffledAnswerOptions = this.createAnswerOptions();

        this.setState({
            ...initialState,
            question: quizQuestions[0].question,
            answerOptions: shuffledAnswerOptions[0]
        })
    }

    render() {
        return (
            <div className="App container">
                <div className={`quizWrapper ${this.final()}`}>
                    {this.state.start?
                        (this.state.result ? this.renderResult() : this.renderQuiz()):
                        this.renderStart()
                    }
                </div>
            </div>
        )
    }
}

export default App;
