import React, {useState} from 'react';
import Question from './Question'
import QuestionCount from './QuestionCount'
import AnswerOption from './QuestionAnswer';
import ProgressBar from "./ProgressBar";
import {CSSTransition, TransitionGroup} from 'react-transition-group';
import PropTypes from 'prop-types'


function Quiz(props) {
    const progressBar = (props.questionId / props.questionTotal) * 100;
    const [isShowAd, setIsShowAd] = useState(false);
    const [adKey, setAdKey] = useState({});
    const adButtonText = (props.questionId === props.questionTotal)?'Показати результат!':'Наступне питання';



    function renderAnswerOptions(key) {
        return (
                <AnswerOption
                    className = {isShowAd ? 'disable': ''}
                    key={key.content}
                    answerContent={key.content}
                    answerType={key.type}
                    answerImage={key.img}
                    answer={props.answer}
                    questionId={props.questionId}
                    onAnswerSelected={handleSelectedAnswer(key)}
                />
        );
    }

    function handleSelectedAnswer(key){
        return (event) => {
            if(key.window?.desc){
                setIsShowAd(true);
                setAdKey(key);
            } else{
                props.onAnswerSelected(event.currentTarget.value);
            }

        }
    }

    const goToNextQuestion = (event) =>{
        setIsShowAd(false);
        setAdKey('');
        props.onAnswerSelected(adKey.type.join(','));
    }

    function createAd (answer) {
        return (
            <div className="question__ad">
                <div className="question__addess" dangerouslySetInnerHTML={ {__html: answer.window.desc} }/>
                <img src={answer.window.render} alt=""
                     className="question__adimg" />
                <button className="nextbutton startthegame" onClick={goToNextQuestion}>{adButtonText}
                    <svg width="24" height="24" xmlns="http://www.w3.org/2000/svg"
                         >
                        <path
                            d="M21.883 12l-7.527 6.235.644.765 9-7.521-9-7.479-.645.764 7.529 6.236h-21.884v1h21.883z"></path>
                    </svg></button>
            </div>
        )
    }

    return (

        <div className="quiz">
            <ProgressBar progressBar={progressBar} />
                             <QuestionCount
                         counter={props.questionId}
                         total={props.questionTotal}
            />
            <Question content={props.question}/>
            <ul className="answerOptions" >
                {props.answerOptions.map(item =>{
                    if((!adKey.variant) || adKey.variant === item.variant){
                      return renderAnswerOptions(item);
                    }
                })}
            </ul>
            {isShowAd ? createAd(adKey) : ''}

        </div>
    );
}

Quiz.propTypes = {
    answer: PropTypes.string.isRequired,
    answerOptions: PropTypes.array.isRequired,
// counter: PropTypes.number.isRequired,
    question: PropTypes.string.isRequired,
    questionId: PropTypes.number.isRequired,
    questionTotal: PropTypes.number.isRequired,
    onAnswerSelected: PropTypes.func.isRequired
};
export default Quiz;